@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;500;&family=Roboto:wght@300;400;600&display=swap);
body {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
}

.img{
  max-width: 100%;
  height: auto;
  display: block;
}

.MuiTypography-root .subtitle {
  color: #A38B74;
  font-size: 75%;
  font-weight: 400;
  display: block;
}

.color-1{
  color: #3e2723;
}

.color-2{
  color: #76976c;
}

.color-3{
  color: #A38B74;
}

.color-4{
  color: #a5c89a;
}


.background-1{
  background-color: #3e2723;
}

.background-2{
  background-color: #76976c;
}

.background-3, .MuiDivider-root.background-3{
  background-color: #A38B74;
}

.background-4{
  background-color: #a5c89a;
}


.text-light{
  color: rgba(255, 255, 255, 0.9);
}

.center {
  text-align: center;
}

.strong{
  font-weight: bold;
}

.padding-top{
  padding-top: 10px;
}

.padding-top-lg{
  padding-top: .75em;
}

.relative{
  position: relative;
}

.MuiGrid-direction-xs-column{
  flex-flow: column nowrap;
}

.image-hidden-xs{
  display: none;

}

@media only screen and (min-width: 960px){
  .image-hidden-xs{
    display: block;
  
  }
}

.appbar.MuiAppBar-root{
    padding: 1em;
}

.logo{
max-width: 120px;
display: inline-block;
text-align: center;
text-decoration: none;
}

.logo svg{
width: 100%;
}

.cls-1{fill:#372B15;}
.cls-2{fill:none;stroke:#372B15;stroke-miterlimit:10;stroke-width:6px;}

.MuiTypography-caption.logo-text{
text-transform: uppercase;
font-weight: 600;
font-size: 0.65rem;
color: #4e342e;

}


.toolbar.MuiToolbar-root {
    justify-content: center;
}

.navigation-btn.MuiIconButton-root{
    position: absolute;
    right: 1%;
}

.navigation-btn .MuiSvgIcon-root{
    width: 1.25em;
    height: 1.25em;
}

.active.MuiListItem-root{
    background-color: rgba(0, 0, 0, 0.04);
}

.active .MuiTypography-body1{
    font-weight: bold;
}
/* hero css */
.hero{
    background: url('https://images.unsplash.com/photo-1606859309981-270838d57ed8?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1189&q=80') no-repeat center;
    height: 80vh;
    min-height: 400px;
    max-height: 900px;
    display: grid;
    grid-template-columns: repeat(5, [col] 20%);
    grid-template-rows: repeat(1, [row] 100% );
    padding-bottom: 4em;
}


.hero-image-1, .hero-image-2{
    display: none;
}

.hero-content{
    grid-column: col 1 / span 5;
    grid-row: row 1 / span 1;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    text-align: left;
    padding: 3em;
    z-index: 10;
}

@media (min-width: 992px) {
    .hero{
        background: none;
        grid-template-columns: repeat(10, [col] 10%);
        grid-template-rows: repeat(9, [row] 11% );
        height: auto;
        
    }

    .hero-image-1, .hero-image-2{
        display: block;
    } 
    
    .hero-image-1{
        grid-column: col 1 / span 4;
    }

    .hero-image-2{
        grid-column: col 3 / span 8;
        grid-row: row 4;
    }
    
    .hero-content{
        grid-column: col 5 / span 6;
        grid-row: row 1 / span 3;
        text-align: left;
        padding-left: 1em;
    }
    
}


form, .hero-form .MuiButtonGroup-root {
    width: 100%;
}

.hero-form .MuiFormControl-root{
    width: 75%;
}

.hero-form .MuiInputLabel-outlined.MuiInputLabel-shrink, .hero-form .MuiOutlinedInput-root{
    background-color: rgba(255, 255, 255, 0.708);
}

.hero-form .MuiButtonBase-root{
    width: 25%;
}
.card.MuiCard-root {
    max-width: 500px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 17%);
    display: flex;
    flex-flow: column;
    justify-content: space-between;

    
}

.card:hover, .card:active {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    box-shadow: 0 4px 20px 0 rgb(0 0 0 / 12%);
}

.card .MuiCardMedia-root{
    height: 500px;
    width: 100%;
    
}

.card-actions{
    justify-content: space-between;
}


.park-details{
    width: 100%;
    height: auto;
    min-height: 800px;
    border-radius: 5px;
    border: 1px solid #e4e4e4;
    margin-bottom: 2em!important;
}

.details-left{
    display: flex;
    flex-flow: column nowrap;
}
/* nav */
.details-menu{
    text-align: left;
    padding: 1em 2em;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.details-menu .MuiListItem-root:hover, .details-menu .MuiListItem-root:active{
    opacity: .75;
}

.details-menu .MuiListItem-root.active::before {
    content: '';
    width: 20px;
    height: 20px;
    background-color: #76976c;
    border-radius: 100%;
    position: absolute;
    left: -22px;
  }

  .details-menu .MuiList-root{
      padding-left: 1em;
  }
  

  /* info */
.details-info{
    text-align: left;
    padding: 2em;
    max-height: 800px;
    overflow: scroll;
    flex-grow: 1;
}

.hours .MuiListItem-gutters{
    padding-left: 0;
    padding-bottom: 5px;

}

.address.MuiList-padding{
    padding-left: 0;
    padding-right: 0;
}

.address .MuiListItem-gutters{
    padding-left: 0;
    padding-bottom: 2px;
    padding-top: 2px;

}


/* about */
.details-about{
    text-align: left;
    padding: 1em 2em;
    position: relative;
}

.details-right{
    flex-flow: column nowrap;
    overflow: scroll;
}

/* fees */
.details-fees{
    text-align: left;
    width: 100%;
    padding: 1em 2em;
    background-color: rgba(62, 39, 35, 0.8);
    
}

/* acitivities */
.details-activities{
    flex-grow: 1;
}

.details-activity-item{
    width: 50%;
    padding: 1em;
    max-height: 500px;
    overflow: scroll;
}

.details-activity-item.item-1{
    border-bottom-left-radius: 5px;
}

.details-activity-item.item-2{
    border-bottom-right-radius: 5px;
}


/* weather */
.weather-icon{
    width: 100%;
    text-align: center;
}

.weather-icon .MuiSvgIcon-root{
    width: 70%;
    height: 70%;
}


@media only screen and (min-width: 960px){
    .details-menu{
        border-top-right-radius: 0;
    }

    .details-info{
        border-bottom-left-radius: 5px;
    }


    .details-activity-item.item-1{
        border-bottom-left-radius: 0;
    }
  }


  /* camping */
  .info-window{
      max-width: 300px;
  }

  .info-window .MuiCardMedia-root{
    width: 100%;
    height: 200px;
  }


  .details-camping{
    padding: 1em 2em;
    overflow: scroll;
  }

  .map-modal{
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba(118, 151, 108, 0.75);
      display: flex;
      align-items: center;
      justify-content: center;
  }
